.container {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: #343a40; }

.container > form {
  margin-top: 10%;
  color: #fff; }

.container .icon {
  background-image: url(./images/react-icon.png);
  height: 120px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  margin: 0 0 20px 0; }

:global .form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: 0 auto; }

:global .form-signin .checkbox {
  font-weight: 400; }

:global .form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px; }

:global .form-signin .form-control:focus {
  z-index: 2; }

:global .form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0; }

:global .form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0; }
